<template>
  <div class="cartoons">
    <!-- <div class="tank"> -->
    <div>
      <van-nav-bar title="漫画" left-text="返回" left-arrow @click-left="back" />
    </div>
    <div class="cartoons-box">
      <div class="classify-box">
        <div class="car-box" ref="vidosbox" @scroll="lazyLoad">
          <div
            class="ping-box"
            v-for="(item,index) in cartoonsData.data.list"
            :key="index"
            ref="pingitem"
            @click="particularsGo({name: 'particulars', params: {url: item.url}})"
          >
            <div class="ping">
              <div class="pho">
                <van-image :src="item.cover">
                  <template v-slot:error>
                    <img class="auto-img" src="../../assets/image/404.jpg" alt />
                  </template>
                </van-image>
                <span class="one-text">{{item.time}}</span>
                <p class="one-text">{{item.latest}}</p>
              </div>
              <div class="infor">
                <h3 class="one-text">{{item.name}}</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("cartoonsModule");

export default {
  name: "cartoons",

  computed: {
    ...mapState(["cartoonsData", "isInit", "isHas", "vidosBoxData"])
  },

  data() {
    return {
      active: 0,
      mhlb: []
    };
  },

  methods: {
    ...mapMutations(["resetState"]),

    // 返回上一步
    back() {
      this.$router.go(-1);
    },

    isInitData() {
      if (this.isInit) {
        let vidosBox = this.$refs.vidosbox;
        // console.log("vidosBox ==> ", vidosBox);

        let vidosBoxH = vidosBox.clientHeight;
        // console.log("vidosBoxH ==> ", vidosBoxH);

        this.$store.commit("cartoonsModule/changevidosBox", {
          vidosBox,
          vidosBoxH
        });
      }
    },
    getcartoonsData() {
      let self = this;

      this.$toast.loading({
        duration: 0,
        message: "加载中..."
      });

      let mhlb = this.$route.query.mhlb;
      // console.log(" this.$route.query ==>> ", this.$route.query);
      // console.log(" mhlb ==>> ", mhlb);

      // 推荐
      this.axios({
        // 请求参数
        methods: "GET",
        url: mhlb
      })
        .then(result => {
          // console.log("result ==>> ", result);

          this.isInitData();

          this.$store.commit("cartoonsModule/changecartoons", result);

          this.$toast.clear();
        })
        .catch(err => {
          // console.log("err ==>> ", err);
          this.$toast.clear();
        });
    },

    lazyLoad() {
      let self = this;

      if (!self.isHas) {
        return;
      }

      let timer = setTimeout(function() {
        for (let i = 1; i < self.vidosBoxData.timers.length; i++) {
          clearTimeout(self.vidosBoxData.timers[i]);
        }

        let pingitem = self.$refs.pingitem[self.$refs.pingitem.length - 1];
        // console.log("pingitem ==> ", pingitem);
        // pingitem 的 全部样式
        let pingitemStyle = pingitem.getBoundingClientRect();
        // console.log("pingitemStyle ==> ", pingitemStyle);
        // pingitem 的 距离可视区域顶部的距离
        let top = pingitemStyle.top;
        // console.log("top ==> ", top);

        let pingitemH = pingitemStyle.height;
        // console.log("pingitemH ==> ", pingitemH);

        if (innerHeight - pingitemH + 80 >= top) {
          // console.log("发起数据请求");
          // 发起数据请求
          self.getcartoonsData();
        }

        self.$store.state.cartoonsModule.vidosBoxData.timers = [];
      }, 1000);
      self.$store.state.cartoonsModule.vidosBoxData.timers.push(timer);
    },

    particularsGo(o) {
      this.$router.push(o);
    }
  },
  created() {
    //重置state
    this.resetState();

    this.getcartoonsData();
  }
};
</script>

<style >
.cartoons .van-image {
  width: 100%;
}
.cartoons .van-image__error {
  width: 100%;
  height: 130px;
}
.cartoons .van-nav-bar .van-icon {
  color: #e4393c;
}
.cartoons .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.cartoons .van-nav-bar__title {
  font-size: 18px;
  color: #e4393c;
  font-weight: bolder;
}
.cartoons .van-search--show-action {
  box-shadow: 0px 0px 5px 0px #999;
}
</style>
<style lang="less" scoped>
.cartoons {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0;
  right: 0;
  overflow-y: auto;
}
.classify-box {
  padding: 0 5px;
  position: absolute;
  top: 0px;
  bottom: 0;
  left: 0;
  right: 0;
}
.cartoons-box {
  padding: 0 5px 10px 5px;
  background: rgb(200, 200, 200);
  overflow: hidden;

  .car-box {
    position: absolute;
    top: 48px;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0 5px 10px 5px;
    overflow-y: auto;
  }

  .ping-box {
    width: calc(~"100% / 3 - 10px ");
    padding: 5px 5px;
    float: left;
    overflow: hidden;
    overflow-y: auto;
    &:nth-child(3n + 1) {
      padding-left: 2.5px;
    }
  }
  .ping {
    border-radius: 10px;
    background: white;
    box-shadow: 3px 3px 3px 3px #ff8e61;
    overflow: hidden;
    .pho {
      width: 100%;
      height: 145px;
      overflow: hidden;
      position: relative;
      span {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        color: white;
        padding: 5px;
        box-sizing: border-box;
        text-align: right;
      }
      p {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        color: white;
        padding: 5px;
        text-align: right;
        box-sizing: border-box;
      }
    }
    .infor {
      h3 {
        text-align: center;
        margin: 5px 0;
        color: #e4393c;
      }
    }
  }
}
</style>